.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  background: #fff;
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
  position: relative;
}

.loader:after {
  content: "";
  box-sizing: border-box;
  width: 12px;
  height: 12px;
  color: #ff3d00;
  background: currentColor;
  border-radius: 50%;
  position: absolute;
  top: 10px;
  left: 6px;
  box-shadow: 25px 2px, 10px 22px;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

body {
  color: #2a2a2a;
